@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.expense-item {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  padding: 0.5rem;
  margin: 1rem 0;
  border-radius: 12px;
  background-color: #4b4b4b;
}

.expense-item__description {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-flex-flow: column-reverse;
          flex-flow: column-reverse;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background-color: #40005d;
  border: 1px solid white;
  padding: 0.5rem;
  border-radius: 12px;
}

@media (min-width: 580px) {
  .expense-item__description {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex: 1 1;
            flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}

.btn {
  background-color: rgb(20, 112, 233);
  color: white;
  border-radius: 7px;
  text-decoration: none;
  cursor: pointer;
  border: none;
  padding: 2px 10px;

}

.main {
  color: white;
}

.DMY{
  color: white;
}
.expenses {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}
.expenseList{

    color: azure;
    padding: 10px;
    margin:15px;

}

.color {
    color: white;
}
.addNew{


   
    border: 10px solid dashed;
    color: blanchedalmond;
    background-color: blueviolet;
    height: 100px;
    margin: 20px;
    padding: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

}

.addNewButton{

    -webkit-justify-content: center;

            justify-content: center;
    background-color: black;
    color: white;
    height: 50px;
    width: 300px;
    border-radius: 20px;
    text-align: center;
    cursor: pointer;

}


.expenseForm{

padding:20px;
}

.expenseForm label{
    display: block;
    color: blanchedalmond;
}

.expenseForm input[type="text"]{
    display: block;
    padding: 10px;
    width: 100%;
    border-radius: 10px;
}

.expenseForm input[type="date"]{
    display: block;
    padding: 10px;
    width: 100%;
    border-radius: 10px;
}

.expenseForm button[type="submit"]{

    background-color: blue;
    color: white;
    padding: 10px 5px;
    border:none;
    margin: 10px;
  

}
.expenseForm button[type="submit"]{

    background-color: green;
    border-radius: 10px;
    color: white;
    padding: 9px 17px;
    border:none;
    margin: 10px;
  
}

.expenseForm button[type="cancel"]{
    background-color: red;
    border-radius: 10px;
    color: white;
    padding: 9px 17px;
    border:none;
    margin: 10px;
  
}
